import React, { Component } from "react";
//form elements
import {
    FormBuilderInput,
    FormBuilderTextArea
} from "./allElements";

class FormBuilder extends Component {
    state = {
        inputCount: 0,
        formElements: [
            {name: "input", description: "this is a input", value: "firstName", type: "text"},
            {name: "input", description: "this is a input", value: "lastName", type: "text"},
            {name: "input", description: "this is a input", value: "email", type: "text"}
        ]
    }

    render(){
        var inputs = {
            notAdded: [],
            added: []
        }
        return (
            <div id="formBuilder">
                <div className="container">
                    <div className="left">
                        <button onClick={this.addInput}>Add Input</button>
                        <button onClick={this.removeInput}>Remove Input</button>
                    </div>
                    <div className="right">
                        <form className="creator">
                            <FormBuilderInput props={this.state.inputCount} />
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}
export default FormBuilder;